        justify-content: space-between;
        max-width: 70%;
        height: 80px;
        margin: 0 auto;
    }
    .logo {
        width: 110px;
        height: 35px;
    }
    .menu {
        font-family: 'Zen Kurenaido';
        display: flex;
        align-items: center;
        font-size: 24px;
        font-weight: 600;
        letter-spacing: 3px;
        color: rgba(255, 255, 255)
    }
    a:link, a:visited {
        color: rgb(0, 0, 0);
        text-decoration: none;
    }
    .about-t, .product-t {
        margin-top: 3px;
    }
    .about-t::after, .product-t::after {
        content: '';
