<template>
    <div class="goods">
        <nav-bar></nav-bar>
        <div class="content">
            <p class="title-g-t">商品一覽</p>
            <div class="search">
                <input type="text" style="width:200px; height:30px;" v-model="name" @input="search" placeholder="請輸入商品名搜尋">
            </div>
            <div class="title-g">
                <div class="good-item" v-for="g, item in showGoods" :key="item">
                    <div class="info">
                        <div class="title">{{g.name}}</div>
                        <div class="price">NT ${{g.price}}</div>
                        <img class="goods-img" :src="g.img">
                        <div class="add-cart" @click="addA(g)">
                            <span>加入購物車</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footer-i></footer-i>
    </div>
</template>

<script>
import NavBar from '@/components/navbar/NavBar';
import FooterI from '@/components/footer/Footer';

import {mapState, mapGetters, mapActions} from 'vuex'

export default {
    name: 'goods',
    data() {
        return {
            name: ''
        }
    },
    components: {
        NavBar,
        FooterI
    },
    methods: {
        search() {
            this.$store.dispatch('goodsList/searchA', this.name)
        },
        ...mapActions('shopCart', ['addA']),
    },
    computed: {
        ...mapState({
            goods: state => state.goodsList.goods
        }),
        // 模塊名, 陣列(要導入的函式)
        ...mapGetters('goodsList', ['showGoods'])
    }
}
</script>

<style>
    .content {
        background-color: rgb(249, 248, 244);
    }
    .title-g-t {
        font-size: 26px;
        text-align: center;
        margin: 0;
        padding: 30px;
        letter-spacing: 2px;
    }
    .search {
        display:inline-block;
        width: 80%;
        text-align: end;
        padding: 20px 0;
    }
    .search input:focus {
        outline:0;
        outline-color: rgba(183, 183, 183);
    }
    .title-g {
        display: flex;
        flex-wrap: wrap;
        width: 70%;
        margin: 0 auto;
        font-size: 26px;
    }
    .good-item {
        display: flex;
        justify-content: center;
        width: calc(100% / 3);
        height: 400px;
        padding-bottom: 30px;
    }
    .goods-img {
        width: 250px;
        height: 250px;
    }
    .info > div {
        font-size: 18px;
        padding: 5px 0;
        letter-spacing: 2.5px;
    }
    .info .title {
        font-weight: 600;
    }
    .add-cart img {
        display: block;
    }
    .add-cart span{
        cursor: pointer;
        width: 100%;
        display: inline-block;
        border: 1px solid #000;
        text-align: center;
        margin-top: 10px; 
        padding: 5px 0;
        font-weight: 600;
        color: rgb(0, 0, 0);
        background-color: rgb(249,248,244);
    }
    .add-cart span:hover {
        color:rgb(255, 255, 255);
        background-color: rgb(38,27,20);
        transition: .5s;
    }

</style>